.app__team{
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__team-portfolio{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;

    .app__team-item{
        width: 190px;
        flex-direction: column;
        margin: 2rem; 
        padding: 0.75rem;
        border-radius: 0.5rem;
        // background-color: var(--white-color);
        cursor: pointer;
        transition: all 0.3s ease;
        // box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
        
        &:hover {
            box-shadow: 0 0 25px rgba(255, 0, 0, 0.2);
        }
        .app__team-img{
            width: 100%;
            height: 150px;
        
            img{
                width: 100%;
                height: 100%; 
                border-radius: 0.5rem;
                object-fit: cover;
            }
        
            @media screen and (min-width: 2000px) {
                height: 350px;
            }
        }
        @media screen and (min-width: 2000px) {
            width: 370px;
            height: 570px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }
        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }
}

.app__team-hover{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;

    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    &:hover {
        background-color: rgba(0,0,0,0.5);

    div{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,0.5);
        color: #fff;

        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;

        cursor: pointer;
        transition: all 0.3s ease;

        svg{
            width: 50%;
            height: 50%;
            color: #fff;
        }
    }
}
}

.app__team-content{
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h4{
        margin-top: 1rem;
        line-height: 1.5;
        text-align: center;
    }
    p{
        text-align: center;
    }
}