.app__mentees{
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__mentees-filter{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;

    margin: 4rem 0 2rem;

    .app__mentees-filter-item{
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        background-color: #ffff;
        color: #000; //color of the text
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover{
            background-color: var(--secondary-color);
            color: #fff;
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;
        }
    }
    .item-active {
        background-color: var(--secondary-color);
        color: #fff;
    }
}
.app__mentees-portfolio{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;

    .app__mentees-item{
        width: 190px;
        flex-direction: column;
        margin: 2rem; 
        // padding: 1rem;
        border-radius: 0.5rem;
        background-color: #fff;
        color: #000;

        cursor: pointer;
        transition: all 0.3s ease;
        
        &:hover {
            box-shadow: 0 0 25px rgba(0,0,0,0.2);
        }
        @media screen and (min-width: 2000px) {
            height: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }
        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }
}

.app__mentees-img{
    width: 100%;
    height: 150px;

    position: relative;

    img{
        width: 100%;
        height: 100%; 
        border-radius: 0.5rem;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        height: 350px;
    }
}

.app__mentees-hover{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;

    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    &:hover {
        background-color: rgba(0,0,0,0.5);

    div{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,0.5);
        color: #fff;

        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;

        cursor: pointer;
        transition: all 0.3s ease;

        svg{
            width: 50%;
            height: 50%;
            color: #fff;
        }
    }
}
}

.app__mentees-content{
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h4{
        margin-top: 1rem;
        line-height: 1.5;
    }
    .app__mentees-tag{
        position: absolute;
        padding: 0.5rem 1rem;
        border-top-left-radius: 10px; 
        border-top-right-radius: 10px; 
        // border-radius: 10px;
        background-color: var(--white-color);
        top: -25px; //go up to the image
    }
}