.app__contact {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.app__contact-portfolio{
display: flex;
justify-content: center;
align-items: flex-start;
flex-wrap: wrap;
margin-top: -3rem;
}
.app__contact-item {
  width: 30%;
  min-height: 320px;
  // background-color: var(--white-color);
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 0.75rem;
  border-radius: 15px;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);

  img {
    width: 80%;
    // height: 50%;
    // border-radius: 50%;
    object-fit: cover;
    justify-items: center;
  }

  // transition: all 0.3s ease-in-out;
  .app__content-item-top{
    display: flex;
    flex-direction: row;
    justify-content: center;


    .app__contact-item-header{
      display: flex;
      flex-direction: column;
      justify-content: center;
      // padding-left: 2rem;
      // font-size: 2vw;      
    }
  }


  @media screen and (min-width: 2000px) {
    min-height: 450px;

    img {
      width: 150px;
      height: 150px;
    }
  }

  @media screen and (max-width: 850px) {
    width: 100%;
  }

  @media screen and (max-width: 600px) {
    flex-direction: column;
  }
}

.app__review-content {
  flex: 1;
  height: 100%;
  padding: 1rem 1rem;
  text-align: left;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;

  p {
    // font-size: 1rem;
    // line-height: 1rem;
    color: var(--black-color);
    // font-family: var(--font-base);

    @media screen and (min-width: 2000px) {
      font-size: 2rem;
      line-height: 3.5rem;
    }
  }

  h4 {
    font-weight: 600;
    color: var(--secondary-color);
    margin-top: 2rem;
  }

  h5 {
    font-weight: 400;
    color: var(--gray-color);
    margin-top: 5px;
  }

  @media screen and (max-width: 600px) {
    margin-top: 2rem;
    padding: 0;
  }
}

.app__review-btns {
  flex-direction: row;
  margin-top: 1rem;

  div {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: var(--white-color);

    margin: 1rem;
    transition: all 0.3s ease-in-out;

    svg {
      width: 20px;
      height: 20px;
      color: var(--secondary-color);
    }

    &:hover {
      background-color: var(--secondary-color);

      svg {
        color: var(--white-color);
      }
    }

    @media screen and (min-width: 2000px) {
      width: 100px;
      height: 100px;

      svg {
        width: 45px;
        height: 45px;
      }
    }
  }
}
